.account-text {
  color: $text-color-main;
  font-family: $font-family--nav;
  font-size: 12px;
  font-weight: 400;
  line-height: 15px;
  text-transform: uppercase;
}

.page-title-h2 {
  color: $text-color-main;
  font-family: "MyriadPro";
  @include fluid-font(25px, 40px, 320px, 1920px);
  font-weight: $font-weight;
}
.page-title-h3 {
  color: $text-color-main;
  font-family: "Abril Fatface";
  @include fluid-font(25px, 40px, 320px, 1920px);
  font-weight: 400;
  letter-spacing: 1px;
  margin-bottom: 30px;
}
.page-title-h4 {
  color: $text-color-main;
  font-family: $font-family--h4;
  @include fluid-font(18px, 18px, 320px, 1920px);
  font-weight: $font-weight--bold;
}

.page-text {
  @include fluid-font(16px, 16px, 320px, 1920px);
  font-family: $font-family;
  font-weight: $font-weight--lite; 
  color: $text-color--p;
}


